@import url('https://fonts.googleapis.com/css?family=Open+Sans|Ranchers');
.container {
  font-family: 'Open Sans', -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell,'Helvetica Neue', sans-serif;
  width: 100vw;
  height: 100vh;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-pack: center;
      justify-content: center;
  background: #b71c1c;
  
  
  
}
div {
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
 
}
.Row {
  display: -ms-flexbox;
  display: flex;
  border-top: 1px solid #ffcdd2;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
}
.Tile {
  border-left: 1px solid #ffcdd2;
}
.Tile:nth-child(4n){
  border-right: 1px solid #ffcdd2;
}
.Row:nth-child(4){
  border-bottom: 1px solid #ffcdd2;
}
.Score {
  text-align: center;
  border-radius: 20px;
  margin-bottom: 50px;
  line-height: 60px;
  font-size: 25px;
  color: white;
  width: 300px;
  height: 60px;
}
.Board {
  color: white;
  overflow: hidden;
  font-family: 'Ranchers', cursive, monospace;
}
.Tile {
  width: 80px;
  height: 80px;
  /* border: 1px solid lightcoral; */
  text-align: center;
  font-size: 30px;
  line-height: 80px;
  background-color: #DD2C00;
}
.size-0, .size-2, .size-4, .size-8, .size-16, .size-32, .size-64, .size-128, .size-256, .size-512 {
  font-size: 40px;
}
.size-0 {
  background: #F84C67;
}
.size-2 {
  background: #F81B3E;
}
.size-4 {
  background: #D90022;
}
.size-8 {
  background: #A9001B;
}
.size-16 {
  background: #FF9D5A;
}
.size-32 {
  background: #FE6700;
}
.size-64 {
  background: #C85100;
}
.size-128 {
  background: #DF2774;
}
.size-256 {
  background: #AA0048;
}
.size-512 {
  background: #850038;
}
.size-1024 {
  background: #FFC107;
}
.size-2048 {
  background: #FF3D00;
}


